@font-face {
    font-family: 'bs';
    src: url("/static/webfonts/b-sans.otf") format("openType");
  }
  
  
  html, body {
    height: 100%;
    width: 100%;
    margin: 0;
    padding: 0;
  }
  
  * {
    font-family: "bs", sans-serif;
  }
  
  #alert-area {
    margin: 0;
    text-align: center;
  }
  
  #main-area {
    height: 100%;
    width: 100%;
    background: rgba(168, 168, 168, 0.5);
    display: grid;
    grid-template-areas: "register" "footer";
    grid-template-rows: 1fr auto;
  }
  
  #register-area {
    grid-area: register;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    #register {
      -webkit-box-shadow: 0 3px 6px rgba(0, 0, 0, 0.3), inset 0 -3px 3px rgba(0, 0, 0, 0.1);
      box-shadow: 0 3px 6px rgba(0, 0, 0, 0.3), inset 0 -3px 3px rgba(0, 0, 0, 0.1);
      background: lightgray;
      border-radius: 20px;
      height: 50%;
      width: 20%;
      display: grid;
      grid-template-areas: "logo" "form";
      grid-template-rows: auto 1fr;
      justify-items: center;
      #logo-area {
        grid-area: logo;
        text-align: center;
        #logo {
          display: block;
          margin: auto;
          width: 75%;
        }
      }
      #form-area {
        width: 100%;
        grid-area: form;
        #form {
          margin: auto;
          width: 90%;
          #username {
            margin-bottom: 10px;
          }
        }
      }
    }
  }
  
  #footer {
    text-align: center;
    -webkit-box-shadow: 0px -2px 5px 0px rgba(0, 0, 0, 0.3);
    box-shadow: 0px -2px 5px 0px rgba(0, 0, 0, 0.3);
    grid-area: footer;
    background: lightgray;
    color: black;
    padding: 5px;
  }
  
  @media screen and (max-width: 568px) {
    #register-area #register {
      height: 90%;
      width: 90%;
    }
  }
  
  @media screen and (max-width: 1000px) and (min-width: 568px) {
    #register-area #register {
      height: 50%;
      width: 50%;
      #logo-area #logo {
        width: 50%;
      }
    }
  }